import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { UserInterface } from "./login.interface";
import * as bcrypt from "bcryptjs";
import { jwtDecode } from "jwt-decode";
import { Router } from "@angular/router";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  private readonly SECRET_KEY =
    "1f*#y=f*o#qd2r+f$97x$+je83=4@6)n6ra8hi3(g-8l37fqx1";

  constructor(private http: HttpClient, private router: Router) {}

  async login(user: Partial<UserInterface>) {
    debugger;
    const data: Partial<UserInterface> = await this.validateUser(
      user.userName,
      user.password
    );

    if (data) {
      const payload = {
        userName: data.userName,
        fullName: data.fullName,
        role: data.role,
      };

      const header = { alg: "HS256", typ: "JWT" };
      const encodedHeader = btoa(JSON.stringify(header));
      const encodedPayload = btoa(encodeURIComponent(JSON.stringify(payload)));

      const token = `${encodedHeader}.${encodedPayload}.fake-signature`;

      localStorage.setItem("access_token", token);
      this.router.navigate(["/"]);
      return true;
    } else {
      console.log("Login failed!");
      return false;
    }
  }

  async validateUser(
    username: string,
    password: string
  ): Promise<Partial<UserInterface> | null> {
    try {
      const repo: Partial<UserInterface>[] = await this.getUser().toPromise();
      const user: Partial<UserInterface> | undefined = repo.find(
        (e) => e.userName === username
      );

      if (user && user.password) {
        const isMatch = await bcrypt.compare(password, user.password);
        if (isMatch) {
          return user;
        }
      }
      return null;
    } catch (error) {
      console.error("Error validating user:", error);
      return null;
    }
  }

  getUser(): Observable<UserInterface[]> {
    return this.http.get<UserInterface[]>(
      "http://truonganhcharts.github.io/assets/a-v1.json"
    );
  }

  getLoggedInUser(): Partial<UserInterface> | null {
    const token = localStorage.getItem("access_token");
    if (token) {
      try {
        const tokenParts = token.split(".");
        if (tokenParts.length !== 3) {
          throw new Error("Invalid token format");
        }
        const encodedPayload = tokenParts[1];

        const decodedBase64 = atob(encodedPayload);

        const originalJson = decodeURIComponent(decodedBase64);

        const decodedToken = JSON.parse(originalJson);

        return {
          userName: decodedToken.userName,
          fullName: decodedToken.fullName,
          role: decodedToken.role,
        };
      } catch (error) {
        console.error("Failed to decode token:", error);
        return null;
      }
    }
    return null;
  }

  getLoggedInUse1r1(): Partial<UserInterface> | null {
    const token = localStorage.getItem("access_token");
    if (token) {
      try {
        const decodedToken: any = jwtDecode(token);
        return {
          userName: decodedToken.userName,
          fullName: decodedToken.fullName,
          role: decodedToken.role,
        };
      } catch (error) {
        console.error("Invalid token", error);
        this.logOut();
        return null;
      }
    }
    this.logOut();
    return null;
  }

  async hashPass(pass: string): Promise<string> {
    try {
      const hashedPassword = await bcrypt.hash(pass, 10);
      return hashedPassword;
    } catch (error) {
      return null;
    }
  }

  async logOut() {
    try {
      localStorage.removeItem("access_token");
      this.router.navigateByUrl("auth");
    } catch (error) {
      return null;
    }
  }

  
}
